  import uniRate from '@/components/uni-rate/uni-rate.vue';
  import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
	export default {
		data() {
			return {
        status:'end',
        goods_id:'',
        tabIndex:0,//导航栏切换
				comTab:[],//导航栏数据
        comData:[],//评论数据
        type:0, //评论的类型
        rows:1, //评论第几页
        page:10,//每页的数量 
			};
		},
    onLoad(e) {
      this.goods_id = e.id; 
      this.fetchNum();
      this.fetchData();
    },
    methods:{
      //请求导航数据
      fetchNum(){
        let url = "goods/commentNum";
        let data = {
          goods_id:this.goods_id
        };
        this.$Request.get(url,data).then( res => {
           if(res && res.code==200 && res.data){
             this.comTab = res.data;
           }
        })
      },
      //请求评论数据
      fetchData(){
        uni.showLoading({
              title:'努力加载中....'
          })
        let url = "goods/commentList"
        let data = {
          goods_id:this.goods_id,
          type:this.type,
          rows:this.rows,
          page:this.page
        }
        this.$Request.get(url,data).then( res => {
          if(res && res.code == 200 && res.data){
              this.comData = res.data;
              uni.hideLoading(); //隐藏加载提示
          }
        })
      },
      //切换评论导航按钮
      toggleNav(index,type){
        this.tabIndex = index;
        this.type = type;
        this.fetchData();
      }
    },
    components:{
      uniRate,
      uniLoadMore
    }
	}
